.container .division{
  width: 600px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.container .division button{
  width: 250px;
  height: 60px;
  background: white;
  border: none;
  outline: none;
  border-radius: 5px;
  color:#1f1d1d;
  font-size: 28px;
  box-shadow: 0px 1px 8px rgba(0,0,0,.3);
  cursor: pointer;
  transform: scale(1) translateY(0px);
  transition: 0.3s;
  
}
.container .division button:hover{
  color: #000;
  box-shadow: 0px 0px 0px 1800px rgba(0,0,0,0.3);
  transform: translateY(-3px);
}
.container .division button:active{
  transform: scale(0.98);
}
.container h2{
  color:#fff;
  text-align: center;
  /* transition: 0.5s; */
  cursor: pointer;
}
@media only screen and (min-width: 320px) and (max-width: 480px){
  .container{
	display: flex;
	flex-direction: column;
}
}